@import url(https://fonts.googleapis.com/css2?family=Caveat:wght@400;700&display=swap);
.banner {
  width: 100%;
  height: 100vh;

  color: white;
  background-color: black;
}

.changing-text {
  font-family: "Caveat", cursive;
  font-size: 3rem;
  font-style: regular;
  width: 50%;
}

.changing-text::before {
  content: "";
  -webkit-animation: change-text 8s ease-in-out 1s infinite;
          animation: change-text 8s ease-in-out 1s infinite;
}

@-webkit-keyframes change-text {
  0% {
    content: "pets";
  }

  33% {
    content: "dogs";
  }

  66% {
    content: "cats";
  }

  100% {
    content: "pets";
  }
}

@keyframes change-text {
  0% {
    content: "pets";
  }

  33% {
    content: "dogs";
  }

  66% {
    content: "cats";
  }

  100% {
    content: "pets";
  }
}

.banner button:focus {
  outline: none;
}

@media only screen and (max-width: 768px) {
  .banner {
    height: 100vh;
  }

  .changing-text {
    font-family: "Caveat", cursive;
    font-size: 2rem;
    font-style: regular;
    width: 50%;
  }
}

.about {
  width: 100%;
  height: auto;

  padding: 60px 15%;
}

.about h1 {
  font-size: x-large;
  font-weight: 600;
  text-align: center;

  width: 100%;
}

.about__contents {
  display: flex;
  margin-top: 60px;
}

@media only screen and (max-width: 768px) {
  .about__contents {
    display: inline;
  }
}

.services ul {
  padding: 20px;
  display: flex;
}

@media only screen and (max-width: 768px) {
  .services ul {
    padding: 0px;
    display: block;
    text-align: center;
  }

  .services img {
    margin: auto;
    margin-top: 20px;
  }
}

.desc {
  padding: 20px;
}

.services h2 {
  font-size: large;
  font-weight: 500;
}

.services h3 {
  opacity: 0.7;
}

.trusted_caretakers {
  height: 100%;

  margin-top: 100px;

  border: 1.5px solid rgba(255, 94, 0, 0.5);
  border-radius: 10px;
}

.trusted_caretakers__title {
  display: flex;

  font-size: large;
  justify-content: center;

  padding: 20px 40px;

  background-color: rgba(255, 94, 0, 0.15);
  border-radius: 10px 0px 0px 0px;
}

.trusted_caretakers__title h2 {
  padding: 20px;
  font-weight: 500;
}

.trusted_caretakers ul {
  display: flex;
  padding: 8px 50px;
}

.trusted_caretakers ul img {
  object-fit: contain;
  width: 30px;
}

.trusted_caretakers h3 {
  padding: 10px 20px;
}

@media only screen and (max-width: 768px) {
  .trusted_caretakers__title {
    display: flex;

    font-size: large;
    justify-content: center;

    padding: 15px 10px;

    background-color: rgba(255, 94, 0, 0.15);
    border-radius: 10px 0px 0px 0px;
  }

  .trusted_caretakers__title img {
    width: 40px;
    margin-left: 0.4rem;
  }

  .trusted_caretakers__title h2 {
    padding: 0px;
    padding-left: 15px;
    font-weight: 500;
    font-size: 1rem;
  }

  .trusted_caretakers ul img {
    width: 25px;
    height: auto;
    padding-left: 2px;
  }

  .trusted_caretakers ul {
    display: flex;
    padding: 8px 15px;
  }
}

.review {
  padding: 120px 15%;
}

.review__content {
  height: 500px;
}

.review__content--text {
  color: black;
  font-weight: 700;
  font-size: x-large;

  /* text-align: right; */

  width: 38%;
  height: auto;

  padding: 20px;
  margin-top: -250px;
  margin-left: 55%;

  background-color: white;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 5px;
}

.review__content--text hr {
  border-top: 3px solid orange;
  margin: 20px 0px 20px 0px;
  width: 40%;
}

.review__content--text h2 {
  color: rgba(0, 0, 0, 0.6);
  font-size: medium;
  font-weight: 400;
  text-align: right;

  margin-top: 20px;
  margin-bottom: 10px;
}

.quote {
  margin-left: 1rem;
  opacity: 0.5;
}

@media only screen and (max-width: 768px) {
  .review {
    padding: 60px;
  }

  .review__content--text {
    color: black;
    font-weight: 700;
    font-size: medium;

    text-align: center;

    width: 100%;
    height: auto;

    padding: 20px;

    margin-top: 20px;
    margin-bottom: 20px;
    margin-left: 0px;
    margin-right: 0px;

    background-color: white;
    border: 1px solid orange;
    border-radius: 5px;
  }

  .review__content--text hr {
    display: none;
  }

  .review__content--text h2 {
    color: rgba(0, 0, 0, 0.6);
    font-size: small;
    font-weight: 400;
    text-align: center;
    
    padding-top: 10px;
    margin-top: 0px;
  }
}

.react-datepicker-wrapper {
  display: inline-block;
  border: 1px solid #e2e8f0;
  border-radius: 0.25rem;
  background-color:#ffff;
  color: #718096;
  font-style: bold;
  padding: 0.5rem 0.75rem;
  margin-bottom: 1rem;
}

.react-datepicker__header {
  text-align: center;
  background-color: #ed8936;
  border-bottom: 1px solid #ed8936;
  border-top-left-radius: 0.3rem;
  padding-top: 8px;
  position: relative;
}

.behind {
    z-index: -100   ;
}
